.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #000129;
	display: flex;
	align-items: center;
	justify-content: center;
}

.login_form_container {
	width: 900px;
	height: 500px;
	display: flex;
	border-radius: 15px;
	box-shadow: 6px 6px 6px #6290C3;
	border: 4px solid #C2E7DA;
}

.left {
	flex: 2;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: #1A1B41;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: left;
	color: #6290C3;
	font-size: 15px;
}

.form_container h1 {
	font-size: 40px;
	margin-bottom: 4rem;
	font: 'Manrope', sans-serif;
	border: 2px solid #6290C3;
	color: #BAFF29;
	padding: 15px;
}

.input {
	outline: none;
	border: none;
	width: 370px;
	padding: 15px;
	border-radius: 10px;
	background-color: #C2E7DA;
	font-size: 14px;
	margin-bottom: 25px;
	margin-top: 7.5px;
}

.error_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #BAFF29;
	text-align: center;
}

.right {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: #6290C3;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
}

.right h1 {
	margin-bottom: 1rem;
	color: #1A1B41;
	font-size: 40px;
	align-self: center;
	border: 2px solid #C2E7DA;
	padding: 15px;
}


/* CSS */
.green_btn {
  background-color: #BAFF29;
  border-radius: 10px;
  box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset,rgba(44, 187, 99, .15) 0 1px 2px,rgba(44, 187, 99, .15) 0 2px 4px,rgba(44, 187, 99, .15) 0 4px 8px,rgba(44, 187, 99, .15) 0 8px 16px,rgba(44, 187, 99, .15) 0 16px 32px;
  color: green;
  cursor: pointer;
  display: inline-block;
  font-family: CerebriSans-Regular,-apple-system,system-ui,Roboto,sans-serif;
  padding: 7px 20px;
  text-align: center;
  text-decoration: none;
  border: 0;
  font-size: 16px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100px;
  transition: 300ms;
}

.blue_btn {
	background-color: #C2E7DA;
	border-radius: 10px;
	box-shadow: rgba(224, 240, 246, 0.2) 0 -25px 18px -14px inset,
    rgba(224, 240, 246, 0.15) 0 1px 2px,
    rgba(224, 240, 246, 0.15) 0 2px 4px,
    rgba(224, 240, 246, 0.15) 0 4px 8px,
    rgba(224, 240, 246, 0.15) 0 8px 16px,
    rgba(224, 240, 246, 0.15) 0 16px 32px;
	color: #1A1B41;
	cursor: pointer;
	display: inline-block;
	font-family: CerebriSans-Regular,-apple-system,system-ui,Roboto,sans-serif;
	padding: 7px 20px;
	text-align: center;
	text-decoration: none;
	border: 0;
	font-size: 16px;
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
	width: 100px;
	transition: 300ms;
  }


.blue_btn:hover{
	box-shadow: rgba(199, 229, 233, 0.35) 0 -25px 18px -14px inset,
    rgba(199, 229, 233, 0.25) 0 1px 2px,
    rgba(199, 229, 233, 0.25) 0 2px 4px,
    rgba(199, 229, 233, 0.25) 0 4px 8px,
    rgba(199, 229, 233, 0.25) 0 8px 16px,
    rgba(199, 229, 233, 0.25) 0 16px 32px;
	transform: scale(1.2);
}
.green_btn:hover {
	box-shadow: rgba(44,187,99,.35) 0 -25px 18px -14px inset,rgba(44,187,99,.25) 0 1px 2px,rgba(44,187,99,.25) 0 2px 4px,rgba(44,187,99,.25) 0 4px 8px,rgba(44,187,99,.25) 0 8px 16px,rgba(44,187,99,.25) 0 16px 32px;
	transform: scale(1.2);
}

